@mixin font-face($font-family, $url) {
  @font-face {
    font-family: '#{$font-family}';
    src: url('#{$url}.eot');
    src: url('#{$url}.eot?#iefix') format('embedded-opentype'),
    url('#{$url}.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
}

@include font-face('comfortaaBold', '../fonts/Comfortaa-Bold');
@include font-face('comfortaaLight', '../fonts/Comfortaa-Light');
@include font-face('comfortaaMedium', '../fonts/Comfortaa-Medium');
@include font-face('comfortaaRegular', '../fonts/Comfortaa-Regular');
@include font-face('comfortaaSemiBold', '../fonts/Comfortaa-SemiBold');


$comfortaaBold:'comfortaaBold';
$comfortaaLight:'comfortaaLight';
$comfortaaMedium:'comfortaaMedium';
$comfortaaRegular:'comfortaaRegular';
$comfortaaSemiBold:'comfortaaSemiBold';

$fonts: $comfortaaRegular;

.comfortaaBold{
  font-family: $comfortaaBold;
}
.comfortaaLight{
  font-family: $comfortaaLight;
}
.comfortaaMedium{
  font-family: $comfortaaMedium;
}
.comfortaaRegular{
  font-family: $comfortaaRegular;
}
.comfortaaSemiBold{
  font-family: $comfortaaSemiBold;
}
