$breakpoint-xxl: 'only screen and (max-width:1700px)';
$breakpoint-xl: 'only screen and (max-width:1200px)';
$breakpoint-lg: 'only screen and (max-width:991px)';
$breakpoint-md: 'only screen and (max-width:768px)';
$breakpoint-sm: 'only screen and (max-width:576px)';
$breakpoint-xs: 'only screen and (max-width:480px)';

$breakpoint-xl-ex: 'only screen and (max-width:1400px)';


.sm-container{
  max-width: 900px;
  margin: 0 auto;
  padding: 0 15px;
  @media #{$breakpoint-lg}{
    max-width: 700px;
  }
  @media #{$breakpoint-md}{
    max-width: 500px;
  }
}
