$community-size:260px;
@keyframes rocket {
  0%{
    transform: none;
  }
  50%{
    transform: translate(10px, -10px);
  }
  100%{
    transform: none;
  }
}
.section{
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.top-section{
  padding-top: 120px;
  background-image: url("../img/bg/bg-1.png");
  background-position: center bottom;
  @media #{$breakpoint-md}{
    padding-top: 90px;
  }
  &__info{
    padding-top: 160px;
    position: relative;
    .rocket{
      position: absolute;
      left: 100px;
      top: 30px;
      width: 110px;
      user-select: none;
      animation: rocket 2s infinite ease-in-out;
    }
    .heading{
      @media #{$breakpoint-md}{
        padding-bottom: 30px;
      }
    }
  }
  &__img{
    padding: 30px 0;
    @media #{$breakpoint-md}{
      max-width: 300px;
      margin: 0 auto;
    }
    img{
      width: 100%;
    }
  }
}
//best buddy section
.best-buddy{
  background-image: url("../img/bg/bg-2.png");
  background-position: center;
  padding: 50px 0;
  &__img{
    padding: 15px 0;
    @media #{$breakpoint-sm}{
      max-width: 300px;
      margin: 0 auto;
    }
    img{
      width: 100%;
    }
  }
  &__info{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
//new buddy
.new-buddy{
  background-image: url("../img/bg/bg-3.png");
  padding-top: 50px;
  padding-bottom: #{$community-size/2};
  &__img{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 15px 0;
    img{
      width: 100%;
    }
  }
}



//community section
.community{
  position: relative;
  z-index: 2;
  &__container{
    background-image: url("../img/bg/bg-section.png");
    background-size: cover;
    margin-top: #{-$community-size / 1.8};
    margin-bottom: #{-$community-size / 1.8};
    background-repeat: no-repeat;
    background-position: top center;
    position: relative;
    border-radius: 20px;
    border: 3px solid transparent;
    background-clip: padding-box;
    @media #{$breakpoint-lg}{
      margin-top: #{-$community-size / 2};
      margin-bottom: #{-$community-size / 2};
    }
    &::before{
      content: '';
      position: absolute;
      z-index: -1;
      border-radius: 20px;
      background: red;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: -3px;
      background: linear-gradient(to top, $green, $pink);
      box-shadow: 0px 5px 30px rgba(0,0,0,0.3);
    }
  }
  &__title{
    text-align: center;
    padding: 18px 40px;
    h4{
      margin: 0;
      color: $white;
      display: block;
      font-size: 18px;
      position: relative;
      @media #{$breakpoint-md}{
        font-size: 16px;
      }
      &::before,
      &::after{
        content: '';
        position: absolute;
        top:50%;
        transform: translateY(-50%);
        height: 3px;
        background-color: $green;
        width: calc(50% - 100px);
        @media #{$breakpoint-md}{
          width: calc(50% - 70px);
        }
      }
      &::before{
        left: 0;
      }
      &::after{
        right: 0;
      }
    }
  }
  &__list{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 10px 50px 30px 50px;
    @media #{$breakpoint-md}{
      padding: 0px 20px 10px 20px;
    }
    a{
      display: flex;
      flex-direction: column;
      width: calc(25% - 50px);
      text-align: center;
      color: $green;
      align-items: center;
      transition: .15s ease;
      @media #{$breakpoint-md}{
        width: calc(25% - 15px);
        font-size: 13px;
      }
      img{
        width: 100%;
        transition: .15s ease;
      }
      &:hover{
        color: $pink;
        img{
          transform: scale(1.05);
        }
      }
    }
  }
}
//roadmap section
.roadmap{
  background-image: url("../img/bg/bg-4.png");
  background-position: center;
  padding: 150px 0;
  text-align: center;
  @media #{$breakpoint-md}{
    padding: 100px 0;
  }
  h3{
    @media #{$breakpoint-md}{
      font-size: 22px;
    }
  }
  .roadmap__img{
    text-align: center;
    max-width: 300px;
    margin: 80px auto 50px auto;
    @media #{$breakpoint-md}{
      max-width: 250px;
    }
    img{
      width: 100%;
    }
  }
  ul{
    padding: 50px 0;
    @extend .sm-container;
    text-align: left;
    @media #{$breakpoint-md}{
      padding: 30px 0;
    }
    li{
      font-size: 22px;
      padding: 10px 0 10px 40px;
      position: relative;
      &::after,
      &::before{
        content: '';
        position: absolute;
      }
      &::before{
        width: 24px;
        height: 24px;
        left: 0;
        top: 12px;
        border-radius: 4px;
        background: linear-gradient(30deg, $pink, $green);
      }
      &::after{
        width: 8px;
        height: 12px;
        border-right: 3px solid $white;
        border-bottom: 3px solid $white;
        left: 8px;
        top: 16px;
        transform: rotate(45deg);
      }
      @media #{$breakpoint-md}{
        font-size: 18px;
      }
    }
  }
  p{
    color: darken($white, 20%);
    margin: 0;
    font-size: 18px;
  }
}
//5th section
.section-5{
  background-image: url("../img/bg/bg-5.png");
  background-position: center;
  padding: 50px 0;
  &__img{
    margin-top: -150px;
    @media #{$breakpoint-md}{
      margin-top: 0;
    }
    img{
      width: 100%;
    }
  }
  &__info{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px 0;
  }
}





.heading{
  padding-bottom: 70px;
  h1,h2,h3,h4,h5,h6{
    position: relative;
    display: block;
    padding-bottom: 10px;
    margin-bottom: 13px;
    color: $white;
    &::after{
      content: '';
      position: absolute;
      height: 3px;
      width: 80%;
      max-width: 300px;
      left: 0;
      bottom: 0;
    }
  }
  &.pink{
    h1,h2,h3,h4,h5,h6{
      &::after{
        background-color: $pink;
      }
    }
  }
  &.green{
    h1,h2,h3,h4,h5,h6{
      &::after{
        background-color: $green;
      }
    }
  }
  p{
    color: $white;
    margin: 0;
  }
  &__main{
    padding-bottom: 40px;
    &.green{
      h1,h2,h3,h4,h5,h6{
        color: $green;
      }
    }
    &.pink{
      h1,h2,h3,h4,h5,h6{
        color: $pink;
      }
    }
    h1,h2,h3,h4,h5,h6{
      padding-bottom: 0;
      &::after{
        display: none;
      }
    }
    p{
      font-size: 18px;
    }
  }
}
.text{
  font-size: 14px;
  color: $white;
  &__important{
    font-size: 16px;
    font-family: $comfortaaBold;
    padding: 20px 0;
  }
}

.scroll-btn{
  position: fixed;
  bottom: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  background: linear-gradient(30deg, $pink, $green);
  box-shadow: 0px 5px 30px rgba(0,0,0,0.3);
  border-radius: 12px;
  transition: .2s;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  z-index: 2;
  &.active{
    visibility: visible;
    opacity: 1;
  }
  &::after{
    content: '';
    position: absolute;
    left: 50%;
    top: 15px;
    transform: translateX(-50%) rotate(45deg);
    border-left: 3px solid $white;
    border-top: 3px solid $white;
    width: 14px;
    height: 14px;
  }
}



































