* {
  margin: 0;
  padding: 0;
}

body {
  font-family: $fonts;
}

a,
a:focus,
a:hover {
  text-decoration: none;
  outline: 0 solid transparent;
}

ul {
  margin: 0;

  li {
    list-style: none;
  }
}

img {
  height: auto;
  max-width: 100%;
}

.bg-img {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}


.font-bold {
  font-weight: 700;
}

.font-light {
  font-weight: 300;
}

