.bt{
  transition: .2s;
  display: inline-block;
  border-style: solid;
  border-width: 2px;
  cursor: pointer;
  text-align: center;
  outline: none!important;
  border-radius: 20px;
  font-size: 16px;
  user-select: none;
  font-family: $comfortaaBold;
  padding:5px 35px 3px 35px;
  text-decoration: none;
  white-space: nowrap;
  &__pink{
    background-color: $pink;
    border-color: $pink;
    color: $white;
    &:hover{
      background-color: darken($pink, 10%);
      border-color: darken($pink, 10%);
      color: $white;
      box-shadow: $btn-shadow;
    }
    &-out{
      border-color: $pink;
      background-color: transparent;
      color: $white;
      &:hover{
        background-color: $pink;
        color: $white;
      }
    }
  }
  &__green{
    background-color: $green;
    border-color: $green;
    color: $black;
    &:hover{
      background-color: darken($green, 10%);
      border-color: darken($green, 10%);
      color: $black;
      box-shadow: $btn-shadow;
    }
    &-out{
      border-color: $green;
      background-color: transparent;
      color: $black;
      &:hover{
        background-color: $green;
        color: $black;
      }
    }
  }
}
