//*header scss goes here
body.hidden{
  overflow: hidden!important;
}
nav{
  padding:30px 0;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  @media #{$breakpoint-md}{
    padding: 20px 0;
  }
  .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media #{$breakpoint-md}{
      max-width: 100%;
    }
  }
  .logo{
    display: flex;
    position: relative;
    z-index: 100;
    a{
      height: 60px;
      @media #{$breakpoint-lg}{
        height: 50px;
      }
      img{
        height: 100%;
      }
    }
  }
  .hamburger{
    display: none;
    position: absolute;
    right: 15px;
    width: 25px;
    height: 18px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 100;
    span{
      height: 2px;
      display: block;
      width: 25px;
      position: absolute;
      right: 0;
      transition: .2s;
      background-color: $white;
      &.t{
        top: 0;
      }
      &.m{
        top: 50%;
        transform: translateY(-50%);
      }
      &.b{
        bottom: 0;
      }
    }
    @media #{$breakpoint-md}{
      display: block;
    }
  }
  ul{
    display: flex;
    align-items: center;
    padding: 0;
    @media #{$breakpoint-md}{
      flex-direction: column;
      position: fixed;
      top: 0;
      right: -102%;
      padding: 90px 15px 20px 15px;
      z-index: 10;
      background-color: $sidebar-color;
      height: 100vh;
      overflow-y: auto;
      width: 100%;
      transition: .1s;
      align-items: flex-start;
      li{
        width: 100%;
      }
    }
    a{
      &:not(.bt-action){
        padding: 5px 0;
        margin: 0 10px;
        color: $white;
        font-family: $comfortaaBold;
        position: relative;
        &.active{
          color: $pink;
        }
        @media #{$breakpoint-lg}{
          font-size: 14px;
        }
        @media #{$breakpoint-md}{
          font-size: 16px;
          display: block;
          margin: 10px 0;
          &:hover{
            color: $pink;
          }
        }
        &::before{
          content: '';
          width: 0;
          height: 2px;
          background-color: $pink;
          position: absolute;
          left: 0;
          bottom: 0;
          transition: .15s ease;
          @media #{$breakpoint-md}{
            display: none;
          }
        }
        &:hover{
          &::before{
            width: 100%;
          }
        }
      }
    }
    .bt-action{
      margin-left: 20px;
      @media #{$breakpoint-lg}{
        margin-left: 10px;
      }
      @media #{$breakpoint-md}{
        width: 100%;
        margin-left: 0;
        margin-top: 10px;
        width: 100%;
      }
    }
  }
  &.active{
    @media #{$breakpoint-md}{
      ul{
        right: 0;
      }
      .hamburger{
        span{
          background-color: $white;
          &.t{
            top: 8px;
            transform: rotate(45deg);
          }
          &.m{
            opacity: 0;
          }
          &.b{
            bottom: 8px;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
}

